
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  width: 1000px;
  margin: 0 auto;
  background-color: #fff;
}

.main-nav {
  background-color: #0c203b;
  text-align: left;
}
.main-nav ul.ul-lev1 {
  height: 60px;
  padding-top: 5px;
  padding-left: 10px;
  font-size: 14px;
  display: inline-block;
}
.main-nav ul.ul-lev1 li.li-lev1 {
  float: left;
  height: 50px;
  line-height: 50px;
  position: relative;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 700;
}
.main-nav ul.ul-lev1 li.li-lev1:hover {
  border-color: #3c6b7a;
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son {
  display: none;
  z-index: 9999;
  width: 280px;
  position: absolute;
  top: 50px;
  left: -1px;
  padding: 4px 0;
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son:hover {
  display: block;
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son ul.ul-lev2 {
  font-weight: 400;
  border: 1px solid #2d505c;
  border-radius: 4px;
  background-color: #0c203b;
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son ul.ul-lev2 li.li-lev2 {
  color: #ccc;
  text-shadow: none;
  height: 40px;
  line-height: 40px;
  border-bottom: 1px solid #2d505c;
  margin-bottom: -1px;
  text-align: left;
  padding-left: 10px;
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son ul.ul-lev2 li.li-lev2:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.main-nav ul.ul-lev1 li.li-lev1 .menu-son ul.ul-lev2 li.li-lev2.active {
  color: #3296fa;
  background-color: rgba(255, 255, 255, 0.1);
}
.main-nav ul.ul-lev1 li.li-lev1 div.nav-title {
  color: #ccc;
  padding: 0 18px;
}
.main-nav ul.ul-lev1 li.li-lev1 div.nav-title.active {
  color: #3296fa;
}
.main-nav div.control {
  float: right;
  color: #2992e0;
  font-size: 14px;
}
.main-nav div.control div.login-status {
  float: left;
  display: inline-block;
  height: 60px;
  line-height: 60px;
  margin-right: 30px;
}
.main-nav div.control div.login-status .user-control span {
  color: #3296fa;
}
.main-nav div.control div.language {
  float: left;
  display: inline-block;
}
.main-nav .clear-float:after {
  content: '';
  height: 0;
  line-height: 0;
  display: block;
  visibility: hidden;
  clear: both;
}

